.auth {
  margin: 3rem auto;
  width: 95%;
  max-width: 25rem;
  border-radius: 6px;
 
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  padding: 1rem;
  text-align: center;
  color: rgb(31, 18, 18);
}

.auth h1 {
  text-align: center;
  color: rgb(37, 35, 35);
}

.control {
  margin-bottom: 0.5rem;
}

.control label {
  display: block;
  color: rgb(182, 158, 158);
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.control input {
  font: inherit;
  background-color: #f1e1fc;
  color: #38015c;
  border-radius: 4px;
  border: 1px solid white;
  width: 100%;
  text-align: left;
  padding: 0.25rem;
}

.actions {
  margin-top: 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.actions button {
  cursor: pointer;
  font: inherit;
  color: white;
  background-color: #5c9acc;
  border: 1px solid #050407;
  border-radius: 4px;
  padding: 0.5rem 2.5rem;
}

.actions button:hover {
  background-color: #9cb6dd;
  border-color: #a57ebe;
}

.actions .toggle {
  margin-top: 1rem;
  background-color: transparent;
  color: #5ca9cc;
  border: none;
  padding: 0.15rem 1.5rem;
}

.actions .toggle:hover {
  background-color: transparent;
  color: #82a0cc;
}